.ContactsForm {
    width: 420px;
}

.ContactsForm .Input {
    font-size: 15px;
    width: 420px;
    font-family: 'SuisseIntl';
    padding: 15px;
    color: #ffffff;
    background-color: #000000;
    border: 1px solid #ffffff;
    margin-bottom: 15px;
    box-sizing: border-box;
}

.ContactsForm .Message {
    resize: none;
    height: 200px;
}

.ContactsForm .SendButton {
    font-size: 15px;
    width: 420px;
    font-family: 'SuisseIntl';
    height: 60px;
    color: #000000;
    background-color: #ffffff;
    border: none;
    font-weight: bold;
    margin-bottom: 15px;
}

.ContactsForm .Input::placeholder {
    color: #ffffff;
}